/* Light Mode */

#searchFilter {
    margin-top: 30px;
}

#searchFilter .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#search-light, #search-dark {
    width: 20%;
}

#search-light i, #search-dark i {
    position: absolute;
    padding: 10px;
    min-width: 40px;
}

.inputSearch-light {
    width: 100%;
    padding: 10px 40px;
    border: none;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
}

#filterRegion {
    width: 100%;
    padding: 10px;
    border: none;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.3);
    font-weight: bold;
    cursor: pointer;
    border-radius: 7px;
}

#wrapper-light {
    background: #fff;
    color: #000;
}

#countries .container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-top: 25px;
}

.countryContent {
    margin: 25px 15px;
    width: 20%;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
    border-radius: 7px;
}

.countryContent img {
    width: 100%;
    border-radius: 7px 7px 0px 0px;
}

.data {
    padding: 20px;
}

.data h2 {
    font-size: 1.2rem;
}

.other p {
    font-size: 0.9rem;
    margin-top: 5px;
    font-weight: 600;
}

.other span {
    font-weight: 300;
}

a {
    text-decoration: none;
    color: #000;
}

/* Dark Mode */
#wrapper-dark {
    background: hsl(207, 26%, 17%);
    color: #fff;
}

.inputSearch-dark {
    background: hsl(207, 26%, 17%);
    width: 100%;
    padding: 10px 40px;
    border: none;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
}